body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
  /*Sticky footer*/
  .Site {
    /* display: flex; */
    min-height: 100vh;
    flex-direction: column;
  }

  .main-content-section {
    flex: 1 1;
    min-height: 65vh;
  }
.jumbotron  {
    background-color: black !important;
    color: red !important;
    font-size: 24 !important;
    border-bottom: 8px solid red;
    text-align: center;
}

.logo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}
.search-btn {
    background-color: red;
    margin-top: 20px;
    color: black !important;
}

.search-btn:hover {
    background-color: black;
    margin-top: 20px;
    color: red !important;
}

.form-label {
    font-size: 20px;
}

.card-header {
    margin-bottom: 30px;
}
.save-btn {
    margin-left: 20px;
}
.footer {
    background-color: #080708 !important;
    margin-top: 80px;
    padding-top: 30px;
    padding-bottom: 30px;
    border-top: 8px solid red;
    color: white !important;
    text-align: center;
}

.footer-heading {
    font-size: 24px;
}

.copy-right {
    color: red !important;
}
